import Vue from 'vue';
import VueMq from 'vue-mq';
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue';
import App from './App.vue';

Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
Vue.use(VueMq, {
	breakpoints: { mobile: 500, tablet: 1200, desktop: Infinity }
});

Vue.config.productionTip = false;

new Vue({
	render: h => h(App)
}).$mount('#app');
